import { Component, OnInit } from '@angular/core';
import { RouteService } from '../route.service';
import { Router } from '@angular/router';
import { WalletService } from 'src/app/shared/services/wallet.service';

@Component({
  selector: 'app-switch-wallet',
  templateUrl: './switch-wallet.component.html',
  styleUrls: ['./switch-wallet.component.scss']
})
export class SwitchWalletComponent implements OnInit {

  accounts_data;
  groupByFilter = 'Type';
  selectedAccount: any;
  constructor(
    private navService: RouteService,
    private accountService: WalletService,
    private router:Router) { }

  ngOnInit() {
    this.navService.account.subscribe(ac => this.selectedAccount = ac);

    this.accountService.getAll().subscribe((accounts) => {
      this.accounts_data = accounts;
    });
  }
  isSelectedAccount(ac) {
    return ac.id == this.selectedAccount.id;
  }
  select(ac) {
    this.navService.confirmAccountValue(ac);
    this.router.navigate(['home/transactions/']);
  }
 
}
