import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { WalletService } from 'src/app/shared/services/wallet.service';
import { RouteService } from '../route.service';

@Component({
  selector: 'app-current-wallet',
  templateUrl: './current-wallet.component.html',
  styleUrls: ['./current-wallet.component.scss']
})
export class CurrentWalletComponent implements OnInit {

  accounts = [];
  isSelectAccount = false;
  selectedAccount: any;
  viewByFilter;
  groupByFilter;

  constructor(public navService: RouteService,
    public router: Router,
    private accountService: WalletService) {
    this.navService.viewBy.subscribe(a => this.viewByFilter = a.range);
    this.navService.groupBy.subscribe(a => this.groupByFilter = a);
  }
  ngOnInit() {
    this.accountService.getAll().subscribe((acc) => {
      this.accounts = acc;
      console.log(acc);
      this.selectedAccount = this.accounts.find(ac => ac.id == this.selectedAccount.id)
    });
    this.navService.account.subscribe(ac => this.selectedAccount = ac);
  }
  displaySelectAccount() {
    this.router.navigate(['home/wallets']);
  }
  select(account) {
    this.navService.confirmAccountValue(account);
    this.selectedAccount = account;
    this.isSelectAccount = false;
  }
  back() {
    this.isSelectAccount = false;
  }

}
