import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CategoriesComponent } from './categories/categories.component';
import { CurrentWalletComponent } from './current-wallet/current-wallet.component';
import { DebtComponent } from './debt/debt.component';
import { HomeComponent } from './home.component';
import { MenuComponent } from './menu/menu.component';
import { SwitchWalletComponent } from './switch-wallet/switch-wallet.component';
import { TransactionsOptionComponent } from './transaction-options/transaction-options.component';
import { TransactionsComponent } from './transactions/transactions.component';
import { TrendComponent } from './trend/trend.component';
import { NavigateBackComponent } from './navigate-back/navigate-back.component';
import { CategoryAddComponent } from './category-add/category-add.component';
import { TransactionAddComponent } from './transaction-add/transaction-add.component';
import { AddWalletComponent } from './add-wallet/add-wallet.component';


const routes: Routes = [
  { path: '', redirectTo: 'transactions', pathMatch: 'full' },
  {
    path: '', component: HomeComponent
    , children: [
      {
        path: 'transactions',
        children: [
          {
            path: '',
            component: TransactionsComponent,
          },
          {
            outlet: 'header-menu',
            path: '',
            component: MenuComponent,
          },
          {
            outlet: 'header-top',
            path: '',
            component: CurrentWalletComponent,
          },
          {
            outlet: 'header-options',
            path: '',
            component: TransactionsOptionComponent,
          }],
      },
      {
        path: 'transaction-add', component: TransactionAddComponent
      },
      {
        path: 'trend', children: [
          {
            outlet: 'header-menu',
            path: '',
            component: MenuComponent,
          },
          {
            path: '',
            component: TrendComponent,
          }
        ]
      },
      {
        path: 'categories', children: [
          {
            path: '',
            component: CategoriesComponent,
          },
          {
            outlet: 'header-menu',
            path: '',
            component: MenuComponent,
          }]
      },
      {
        path: 'category-add', component: CategoryAddComponent
      },
      {
        path: 'debt', children: [
          {
            outlet: 'header-menu',
            path: '',
            component: MenuComponent,
          },
          {
            path: '',
            component: DebtComponent,
          }
        ]
      },
      {
        path: 'wallets', children: [
          {
            path: '',
            component: SwitchWalletComponent
          },
          {
            path: '',
            outlet: 'header-menu',
            component: NavigateBackComponent
          }
        ]
      },
      {
        path: 'wallet-add',
        component: AddWalletComponent
      },


    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HomeRoutingModule { }
