import { Component, OnInit } from '@angular/core';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { KindEnum, TransactionService } from 'src/app/shared/services/transaction.service';
import { RouteService } from '../route.service';
import { WalletService } from 'src/app/shared/services/wallet.service';

@Component({
    selector: 'app-transaction-add',
    templateUrl: './transaction-add.component.html',
    styleUrls: ['./transaction-add.component.scss'],
    // animations: [Animations.slideUp],
})
export class TransactionAddComponent implements OnInit {

    selectedAccount: any;
    private transaction;
    addTransactionForm: FormGroup;
    kindEnum = KindEnum;
    constructor(private router: Router,
        private location: Location,
        private navService: RouteService,
        private accountService: WalletService,
        private transactionService: TransactionService

    ) {
        this.navService.account.subscribe(ac => {
            this.selectedAccount = ac;
            console.log(this.selectedAccount);
        });
    }

    ngOnInit() {
        this.addTransactionForm = new FormGroup({
            note: new FormControl(),
            amount: new FormControl('0', Validators.required),
            category: new FormControl('', Validators.required),
            time: new FormControl(Date(), Validators.required)
        });
    }
    onSubmit({ valid, value }: { valid: any, value: any }) {
        this.transactionService.update({}).subscribe();
        if (valid) {
            value.currency = this.selectedAccount.currency;
            value.accountId = this.selectedAccount.id;
            let tr = this.transactionService.fromForm(value);
            const mutation = {
                accountId: this.selectedAccount.id,
                amount: tr.amount,
                currency: this.selectedAccount.currency
            }
            this.accountService.mutateBalance(mutation);
            this.transactionService.create(tr).subscribe((a) => {
                this.router.navigate(['/home/transactions/']);

            });
        }
    }
    back() {
        this.location.back();
    }
}
