import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { ICategory } from '../models/icategory';

const API_URL: string = 'https://thps6qlf34.execute-api.us-east-1.amazonaws.com/latest/';

@Injectable({
  providedIn: 'root'
})
export class CategoryService {

 
  constructor(private http: HttpClient) { }
  getAll() {
    return this.http.get(API_URL + 'category')
      .pipe(map((categories: ICategory[]) => {
        let categoryMap: ICategory[] = [];
        categories && categories.forEach((category:ICategory) => {
          categoryMap.push({
            id: category.id,
            name: category.name, 
            icon: category.icon,
            type: category.type,
          });
        });
        return categoryMap;
      }));
  }
  create(category) {
    category.name= category.Name;
    category.icon = category.Icon;
    category.type= category.Type;
    return this.http.post(API_URL + 'category', category);
  }
}
