import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ITransaction } from '../models/itransactions';
import { map } from 'rxjs/operators';
import { CurrencyService } from './currency.service';

const API_URL: string = 'https://thps6qlf34.execute-api.us-east-1.amazonaws.com/latest/';
export enum KindEnum {
  EXPENSE = 0,
  INCOME = 1,
  TRANSFER = 2
}

@Injectable({
  providedIn: 'root'
})
export class TransactionService {


  constructor(private http: HttpClient, private Currency: CurrencyService) { }
  getAll() {
    return this.http.get(API_URL + 'transaction')
      .pipe(map((transactions: []) => {
        return transactions
      }));
  }
  create(transaction) {
    return this.http.post(API_URL + 'transaction', transaction);
  }
  update(transaction) {
    return this.http.post(API_URL + 'transaction/T1574844649524', { note: '1234', amount: 1234 });
  }



  fromForm(data) {
    return {
      ...data,
      id: data.id || `T${Date.now()}`,
      amount: this.Currency.toInt(
        data.amount * (data.category.type === KindEnum.EXPENSE ? -1 : 1),
        data.currency
      ),
      category: data.category,
      time: data.time ? new Date(data.time) : undefined
    };
  };
  toForm(data) {
    return {
      ...data,
      amount: this.Currency.toFloat(
        data.amount * (data.category.type === KindEnum.EXPENSE ? -1 : 1),
        data.currency,
        false
      ),
      linkedAmount:
        data.category.type === KindEnum.TRANSFER
          ? this.Currency.toFloat(data.linkedAmount, data.linkedCurrency)
          : undefined,
      category: data.category,
      categoryId: data.category.id,
      accountId: data.accountId,
      time: data.time
    };
  };
  fromStorage(data) {
    return {
      id: data._id,
      date: parseInt(data._id.match(/T([0-9]+)-/)[1], 10),
      ...data
    };
  }
}
