import { Component, OnInit } from '@angular/core';
import { RouteService } from '../route.service';

@Component({
  selector: 'app-transaction-options',
  templateUrl: './transaction-options.component.html',
  styleUrls: ['./transaction-options.component.scss']
})
export class TransactionsOptionComponent implements OnInit {
  viewByFilter: any;
  groupByFilter: string;

  constructor(public navService: RouteService,public nav: RouteService) {
    this.navService.viewBy.subscribe(a => this.viewByFilter = a.range);
    this.navService.groupBy.subscribe(a => this.groupByFilter = a);
   }

  ngOnInit() {
  }
  groupByTransaction() {
    this.nav.confirmGroupBy('time');
  }
  groupByCategory() {
    this.nav.confirmGroupBy('categoryId');
  }
  groupByMonth() {
    this.nav.confirmViewBy('month');
  }
  groupByDay() {
    this.nav.confirmViewBy('day');
  }
  groupByWeek() {
    this.nav.confirmViewBy('week');
  }
  click(){
    console.log("click");
  }

}
