import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { RouteService } from '../route.service';
import { WalletService } from 'src/app/shared/services/wallet.service';
import { TransactionService } from 'src/app/shared/services/transaction.service';
import { CategoryService } from 'src/app/shared/services/category.service';

@Component({
  selector: 'app-add-wallet',
  templateUrl: './add-wallet.component.html',
  styleUrls: ['./add-wallet.component.scss']
})
export class AddWalletComponent implements OnInit {
  addAccountForm: FormGroup;
  constructor(
    private transactionService: TransactionService,
    private accountService: WalletService,
    private categoryService: CategoryService,
    private router: Router,
    private navbarService: RouteService
  ) {

  }

  ngOnInit() {
    this.addAccountForm = new FormGroup({
      name: new FormControl('', Validators.required),
      currency: new FormControl('', Validators.required),
      balance: new FormControl('0'),
      icon: new FormControl('/assets/myicons/ml/icon_59.png')

    });
  }
  onSubmit({ valid, value }: { valid: boolean, value: any }) {
    if (valid) {
      let cur = value.currency.key;
      let balance = new Object;
      balance[cur] = value.balance;
      let account = {
        id: 'wallet' + new Date().getTime().toString(),
        name: value.name,
        balance: balance,
        currency: [cur],
        icon: value.icon
      };

      this.accountService.create(account).subscribe();
      let otherCategory;
      if (value.balance != 0) {
        let categoryId = value.balance < 0 ? 'C0' : 'C1';
        // this.categoryService.getAll(categoryId).then((category) => {
        let tran = {
        id: 'transaction' + new Date().getTime().toString(),
          amount: account.balance,
          category: categoryId,
          currency: value.currency.value,
          time: Date(),
          note: ''
        };
        this.transactionService.create(tran).subscribe((a) => {
          this.navbarService.account.next(account);
          this.router.navigate(['/home/wallets/']);
        });
        // });

      }



    }
  }
}
